import React from "react";
import { HashRouter, Route, Switch } from "react-router-dom";
import { useSelector, useDispatch } from "react-redux";
import cookies from "js-cookie";
import "./scss/style.scss";
import store from "./store/store";
import { getDevices } from "./store/store";
import ConnectMQTT from "./MQTT/mqtt.js";

require("dotenv").config();
const axios = require("axios");
axios.defaults.baseURL = "https://ugauss.herokuapp.com/api"; //"http://localhost:3001/api";

const loading = (
  <div className="pt-3 text-center">
    <div className="sk-spinner sk-spinner-pulse"></div>
  </div>
);
const { login, verify } = require("./endpoint/users");

// Containers
const TheLayout = React.lazy(() => import("./containers/TheLayout"));

const secure = window.location.protocol === "https";

// Pages
const Login = React.lazy(() => import("./views/pages/login/Login"));

const App = () => {
  const isLog = useSelector((state) => state.isLog);
  const dispatch = useDispatch();

  const onLogin = (username, password) => {
    login({ username, password }).then((result) => {
      if (result) {
        cookies.set("jwt", result.data);
        dispatch({ type: "login/isLog", isLog: true });
        store.dispatch(getDevices);
      }
    });
  };

  return (
    <HashRouter>
      <React.Suspense fallback={loading}>
        <Switch>
          <Route path="/" name="Home" render={(props) => <TheLayout {...props} />} />
          <ConnectMQTT />
        </Switch>
      </React.Suspense>
    </HashRouter>
  );
};
export default React.memo(App);
