import React, { useEffect, useState } from "react";
import mqtt from "mqtt";
import { useDispatch } from "react-redux";

const host = "wss://driver.cloudmqtt.com";
const mqttOption = {
  port: 38909,
  keepalive: 30,
  protocolId: "MQTT",
  protocolVersion: 4,
  clean: true,
  reconnectPeriod: 1000,
  connectTimeout: 30 * 1000,
  username: "dhvvrehu",
  password: "10T74SxY2TX2",
  will: {
    topic: "WillMsg",
    payload: "Connection Closed abnormally..!",
    qos: 0,
    retain: false,
  },
  rejectUnauthorized: false,
  clientId: "mqttjs_webClient",
};

const Donnees = () => {
  const [client, setClient] = useState(null);
  const [payload, setPayload] = useState({});
  const [connectStatus, setConnectStatus] = useState("Connect");
  const dispatch = useDispatch();

  useEffect(() => {
    setClient(mqtt.connect(host, mqttOption));
  }, []);
  useEffect(() => {
    mqttSub();
  }, [client]);

  const mqttSub = () => {
    console.log(client);
    if (client) {
      var topic = "Readings";
      const qos = 0;
      client.subscribe(topic, { qos }, (error) => {
        if (error) {
          console.log("Subscribe to topics error", error);
          return;
        }
        console.log("object");
      });
      topic = "Source_Profile";
      client.subscribe(topic, { qos }, (error) => {
        if (error) {
          console.log("Subscribe to topics error", error);
          return;
        }
        console.log("object");
      });
      topic = "Charge_Profile";
      client.subscribe(topic, { qos }, (error) => {
        if (error) {
          console.log("Subscribe to topics error", error);
          return;
        }
        console.log("object");
      });
    }
  };
  useEffect(() => {
    if (client) {
      client.on("message", (topic, message) => {
        switch (topic) {
          case "Charge_Profile":
            dispatch({
              type: "[Charge_Profile] message",
              charge_Profiles: JSON.parse(message.toString()),
            });
            break;
          case "Readings":
            dispatch({
              type: "[Readings] message",
              readings: JSON.parse(message.toString()),
            });
            break;
          case "Source_Profile":
            dispatch({
              type: "[Source_Profile] message",
              source_Profiles: JSON.parse(message.toString()),
            });
            break;
        }
      });
    }
  }, [client]);
  return <></>;
};

export default Donnees;
