const axios = require("axios");

exports.login = (info) => {
  return axios
    .post("/user/login", info)
    .then((response) => {
      return response;
    })
    .catch((error) => {
      console.log(error);
    });
};
exports.verify = (cookie, check) => {
  axios
    .request({
      url: "/user/verify",
      method: "post",
      headers: { Authorization: `Bearer ${cookie}` },
      data: { check },
    })
    .then((response) => {})
    .catch((error) => {
      console.log(error);
    });
};
