import cookies from "js-cookie";
const axios = require("axios");

export async function getObjects(nameFct) {
  let res = await axios
    .request({
      url: nameFct,
      method: "get",
      headers: { Authorization: `Bearer ${cookies.get("jwt")}` },
    })
    .then((response, err) => {
      if (!err) {
        return response;
      }
    })
    .catch((error) => {
      console.log(error);
    });
  return res;
}

export async function addObject(nameFct, data) {
  return axios({
    url: nameFct,
    method: "post",
    data,
    headers: { Authorization: `Bearer ${cookies.get("jwt")}` },
  })
    .then((response) => {
      console.log(response);
    })
    .catch((error) => {
      console.log(error);
    });
}

export async function editObject(nameFct, data) {
  return axios({
    url: nameFct,
    method: "put",
    data,
    headers: { Authorization: `Bearer ${cookies.get("jwt")}` },
  })
    .then((response) => {
      console.log(response);
    })
    .catch((error) => {
      console.log(error);
    });
}
