import { createStore, applyMiddleware } from "redux";
import thunkMiddleware from "redux-thunk";
import { composeWithDevTools } from "redux-devtools-extension";
const { getObjects } = require("../endpoint/object");

const composedEnhancer = composeWithDevTools(applyMiddleware(thunkMiddleware));

const initialState = {
  sidebarShow: "responsive",
  devices: [],
  editMode: null,
  isLog: false,
};

export async function getDevices(dispatch, getState) {
  const response = await getObjects("/device");
  if (response) {
    console.log(response);
    dispatch({ type: "device/getDevices", devices: response.data });
    dispatch({ type: "login/isLog", isLog: true });
  } else {
    dispatch({ type: "login/isLog", isLog: false });
  }
}

const changeState = (state = initialState, { type, ...rest }) => {
  switch (type) {
    case "setNavbar":
      return { ...state, ...rest };

    case "editMode":
      return { ...state, ...rest };
    case "device/getDevices":
      return { ...state, ...rest };
    case "login/isLog":
      return { ...state, ...rest };
    case "[Readings] message":
      return { ...state, ...rest };
    case "[Charge_Profile] message":
      return { ...state, ...rest };
    case "[Source_Profile] message":
      return { ...state, ...rest };
    default:
      return state;
  }
};

const store = createStore(changeState, composedEnhancer);
export default store;
